.upcoming {
    position: fixed;
    top: 52%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 85%;
}

.upcomingP {
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 40px;
    text-decoration: underline;
}

.games {
    position: absolute;
    top: 12%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    padding: 0;
    text-align: center;
    display: inline-block;
}

.games li {
    background-color: rgba(0, 0, 0, 0.5);
    width: 60%;
    margin-top: 20%;
    height: 94px;
    display: inline-block;
    border-radius: 15px;
}

.names {
    margin-top: 6%;
    width: 80%;
    height: 90px;
    display: inline-block;
}

.little {
    margin: 0 !important;
    font-size: 20px;
}

.first {
    margin-top: 5% !important; 
}