.navbar {
    position: fixed;
    width: 100%;
    height: 10%;
    left: 0;
    top: 0;
    background: rgb(28, 33, 40)
}

.stats {
    position: fixed;
    left: 40%;
    height: 60%;
    width: 50%;
    top: 60%;
}

.name {
    position: absolute;
    top: 40%;
    transform: translate(-50%, -50%);
    left: 50%;
    font-family: 'playerTitles';
    font-size: 35px;
}
.lastName {
    position: absolute;
    top: 55%;
    left: 55%;
    transform: translate(-50%, -50%);
    font-size: 70px;
    font-family: 'playerTitles';

}

.top {
    position: fixed;
    height: 30%;
    top: 8%;
    width: 100%;
}
.top img {
    position: absolute;
    top: 50%;
    width: 14%;
    right: 2%;
    transform: translate(-50%, -50%);
}

.will {
    position: fixed;
    height: 70%;
    top: 8%;
    left: 0%;
}   


.bg {
    position: fixed;
    height: 62%;
    width: 100%;
    bottom: 0;
    background-color: #17181b;
    z-index: 100;
}

.currentStats {
    position: absolute;
    top: 0;
    height: 10%;
    width: 100%;
    background-color: #0f0f11;
}
.currentStats p {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    font-size: 35px;
    top: 50%;
}
