@font-face {
  font-family: 'main'; /*a name to be used later*/
  src: url('./assets/Bebas-Regular.ttf'); /*URL to font*/
}

* {
  margin: 0px;
  text-decoration: none;
  color: white;
  font-family: main;
}

#App {
  position: fixed;
  width: 100%;
  height: 100%;
  text-align: center;
  /*background-color:rgb(40, 44, 52) !important; 
  background-color: rgba(40, 44, 52, 0.5);*/
}

.bgimg {
  position: fixed;
  width: 100%;
  height: 92%;
  top: 8%;
  left: 0;
  background-image: url("./assets/bgimg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

a:visited {
  color: white;
}
a {
  text-decoration: none;
  color: white;
}

.info {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  top: 38%;
  height: 20%;
  width: 100%;
  color: white;
  font-size: 100px;
  text-align: center;
}
.info p {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 100%;
}

.meetLink {
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 45%;
  width: 50%;
  height: 8%;
  border-radius: 15px;
  background-color: #17181b;
}
.meetLink a {
  position: absolute;
  font-size: 45px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}

.followUs {
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 72%;
  font-size: 50px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 5px black;
}
/*.followUs:hover {
  padding: 21px;
  font-size: 51px;
}*/

.tiktok {
  position: fixed;
  width: 75px;
  height: 75px;
  top: 80%;
  left: 40%;
  transform: translate(-50%, -50%);
}
.youtube {
  position: fixed;
  width: 75px;
  height: 75px;
  top: 80%;
  right: 32%;
  transform: translate(-50%, -50%);
}

.navBar {
  position: fixed;
  width: 100%;
  height: 8%;
  background-color: #17181b;
  margin: 0;
  padding: 0;
}

.navlist {
  margin: 0;
  list-style-type: none;
  text-align: center;
  display: flex;
  align-items: stretch; /* Default */
  justify-content: space-between;
  width: 65%;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 12%;
  
}

li {
  display: block;
  flex: 0 1 auto; /* Default */
  list-style-type: none;
  font-size: 30px;
}

.logo {
  position: absolute;
  height: 70%;
  width: 10%;
  left: 6%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.logo img {
  width: 100%;
  height: 100%;
}

.sixth {
  position: fixed;
  width: 35%;
  height: 6%;
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
}




.contactPopUp {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  height: 50%;
  width: 80%;
  border-radius: 30px;
}

.title {

}

.darkened {
  filter: brightness(25%);
}

.pointer {
  cursor: pointer;
}